
body {
  font-family: 'Segoe UI', sans-serif;
  background-color: #fff8f0;
  margin: 0;
  padding: 0;
  color: #333;
}

nav {
  background-color: #f9c9c0;
  padding: 1rem;
  display: flex;
  gap: 2rem;
  justify-content: center;
  border-bottom: 2px solid #f1b5aa;
}

nav a {
  text-decoration: none;
  color: #5a2a27;
  font-weight: 600;
  font-size: 1.1rem;
}

nav a:hover {
  color: #8c3b36;
}

h2 {
  color: #8c3b36;
  text-align: center;
}

button {
  background-color: #ffb199;
  border: none;
  padding: 0.5rem 1rem;
  margin: 0.25rem;
  cursor: pointer;
  border-radius: 8px;
  font-weight: bold;
  transition: background-color 0.2s ease;
}

button:hover {
  background-color: #ff9274;
}

input, textarea {
  border: 1px solid #ccc;
  border-radius: 6px;
  padding: 0.5rem;
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 1rem;
}

form {
  max-width: 600px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: #fff0ea;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.05);
}

ul {
  list-style: none;
  padding: 0;
  max-width: 600px;
  margin: 0 auto;
}

li {
  background-color: #fff5f2;
  padding: 1rem;
  margin-bottom: 0.5rem;
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #f0ddd8;
}
